.ax-input {
    display: flex;
}

.ax-input .your-name {
    position: relative;
    margin-right: 30px;
}

.ax-input .icon-user {
    position: absolute;
    right: 7px;
    top: 5px;
}

.ax-input .email {
    position: relative;
}

.ax-input .icon-email {
    position: absolute;
    right: 7px;
    top: 5px;
}

.ax-input .input {
    background: #f0f0f0;
    border-radius: 16px;
    height: 48px;
    width: 248px;
    border: none;
    padding: 0 15px;
}

::placeholder {
    color: Black;
    opacity: 1;
}

.textarea-icon {
    position: relative;
    padding: 30px 0px;
    margin-right: 30px;
}

.textarea {
    height: 80px;
    width: 370px;
    padding: 15px;
    background: #f0f0f0;
    border: 2px solid #6C5CE7;
    border-radius: 16px;
}

.icon-edit {
    position: absolute;
    right: 3px;
}

.ax-textarea {
    display: flex;
    align-items: center;
}

.ax-textarea .button {
    width: 170px;
    height: 80px;
    border: none;
    color: #fff;
    font-weight: bold;
    font-size: 24px;
    line-height: 36px;
    letter-spacing: 0.25px;
    background: #6C5CE7;
    box-shadow: 0px 8px 24px rgba(38, 50, 56, 0.1), 0px 16px 32px rgba(38, 50, 56, 0.08);
    border-radius: 16px
}

@media screen and (max-width: 1096px) {
    .ax-input .input {
        width: 100%;
    }

    .textarea {
        width: 100%;
    }

    .ax-input .your-name {
        margin-right: 5px;
    }

    .textarea-icon {
        margin-right: 5px;
    } 
}

@media screen and (max-width: 900px) {
    .ax-textarea {
        display: flex;
        flex-direction: column;
    }

    .ax-input {
        display: flex;
        flex-direction: column;
    }

    .textarea-icon {
        width: 100%;
    }

    .ax-input .input {
        margin: 11px 0;
    }
}